import {isNullOrUndefined} from 'util';
export class Action {
    constructor(public tool: ToolType, public size: number, public color: Color, public pointArray?: Point[]) {
        this.tool = tool;
        this.size = size;
        this.color = color;
        this.pointArray = isNullOrUndefined(pointArray) ? [] : pointArray;
    }

    addPoint(point: Point) {
        this.pointArray.push(point);
    }

    getPointCount() {
        return this.pointArray.length;
    }

    cleanCopy(): Action {
        return new Action(this.tool, this.size, this.color);
    }
}

export class Color {
    constructor(public r: number, public g: number, public b: number, public a?: number) {
        this.r = r;
        this.g = g;
        this.b = b;
        this.a = a === null ? 255 : a;
    }
}

export class Point {
    constructor(public x: number, public y: number) {
        this.x = x;
        this.y = y;
    }

    comparePoint(oPoint: Point) {
        return this.x === oPoint.x && this.y === oPoint.y;
    }
}

export enum InteractionType {
    Clear,
    Undo
}

export enum SizeType {
    Small = 5,
    Medium = 12,
    Large = 22
}

export enum ToolType {
    Pencil,
    Eraser,
    PaintBucket,
    Line,
    Circle,
    Triangle,
    Rectangle
}


