import {Injectable}     from '@angular/core';
import {Http, URLSearchParams} from '@angular/http';
import {Observable}     from 'rxjs/Observable';
import {Room} from '../util/classes';
import {Options, Identification} from '../util/interfaces';
import {DataServiceUtil} from '../util/dataservice.util';

@Injectable()
export class RoomsDataService {

    constructor(private http: Http) {
    }

    private roomsApiUrl = process.env.API_URL + 'rooms';

    getRoomArray(): Observable<Room[]> {
        return this.http.get(this.roomsApiUrl)
            .map(DataServiceUtil.extractData)
            .catch(DataServiceUtil.handleError);
    }

    createRoom(options: Options): Observable<number> {
        return this.http.post(this.roomsApiUrl, options)
            .map(DataServiceUtil.extractData)
            .catch(DataServiceUtil.handleError);
    }

    private roomApiUrl = this.roomsApiUrl + '/room';

    joinRoom(identification: Identification): Observable<Room> {
        let params: URLSearchParams = new URLSearchParams();
        params.set('id', identification.id);
        params.set('password', identification.password);

        return this.http.get(this.roomApiUrl, {search: params})
            .map(DataServiceUtil.extractData)
            .catch(DataServiceUtil.handleError);
    }

    joinPublicRoom(): Observable<Room> {
        let params: URLSearchParams = new URLSearchParams();
        params.set('id', '-1');

        return this.http.get(this.roomApiUrl, {search: params})
            .map(DataServiceUtil.extractData)
            .catch(DataServiceUtil.handleError);
    }
}



