import {Component, Input} from '@angular/core';
import {GameService} from '../../common/service/game.service';
import {InteractionType, SizeType, ToolType} from '../draw/engine/draw.classes';
import {EnumUtil} from '../../common/util/enum.util';

@Component({
    selector: 'dt-tools',
    template: require('./tools.component.html'),
    styles: [require('./tools.component.scss')]
})

export class ToolsBlockComponent {
    interactionTypes: number[] = EnumUtil.getValues(InteractionType);
    sizeTypes: number[] = EnumUtil.getValues(SizeType);
    toolTypes: number[] = EnumUtil.getValues(ToolType);
    @Input() currentSize: SizeType;
    @Input() currentTool: ToolType;

    constructor(private gameService: GameService) {
    }

    executeAction(interaction: InteractionType) {
        this.gameService.executeAction(interaction);
    }

    changeSize(size: SizeType) {
        this.currentSize = size;
        this.gameService.changeSize(size);
    }

    changeTool(tool: ToolType) {
        this.currentTool = tool;
        this.gameService.changeTool(tool);
    }
}



