import {Component, Input} from '@angular/core';
import {RoomsDataService} from '../../common/dataservice/rooms.dataservice';
import {ToastService} from '../../common/service/toast.service';

@Component({
    selector: 'dt-options',
    styles: [require('./options.component.scss')],
    template: require('./options.component.html')
})

export class OptionsComponent {
    @Input() userName: string;

    constructor(private roomsDataService: RoomsDataService,
                private toastService: ToastService) {
    }

    findRoom() {
        this.roomsDataService.joinPublicRoom().subscribe(
            data => {
                console.log(data);
            },
            error => {
                this.toastService.warning(error.message);
            });
    }
}


