import {Injectable} from '@angular/core';
import {ToastyService, ToastyConfig, ToastOptions, ToastData} from 'ng2-toasty';

@Injectable()
export class ToastService {
    private static lastToastId: number = null;

    constructor(private toastyService: ToastyService, private toastyConfig: ToastyConfig) {
        this.toastyConfig.theme = 'bootstrap';
    }

    clearToasts() {
        this.toastyService.clearAll();
    };

    showLoading() {
        this.toastyService.wait(this.generateToastOptions('Loading...'));
    }

    error(title: string, message?: string) {
        this.toastyService.error(this.generateToastOptions(title, message));
    }

    info(title: string, message?: string) {
        this.toastyService.info(this.generateToastOptions(title, message));
    }

    success(title: string, message?: string) {
        this.toastyService.success(this.generateToastOptions(title, message));
    }

    wait(title: string, message?: string) {
        this.toastyService.wait(this.generateToastOptions(title, message));
    }

    warning(title: string, message?: string) {
        this.toastyService.warning(this.generateToastOptions(title, message));
    }

    private generateToastOptions(title: string, message?: string): ToastOptions {
        return {
            title: title,
            msg: message,
            showClose: false,
            timeout: 2500,
            theme: 'bootstrap',
            onAdd: (toast: ToastData) => {
                if (ToastService.lastToastId != null) {
                    this.toastyService.clear(ToastService.lastToastId);
                }

                ToastService.lastToastId = toast.id;
            },
            onRemove: null
        };
    };
}


