import {Pipe, PipeTransform} from '@angular/core';
import {ToolType, SizeType, InteractionType} from '../../game/draw/engine/draw.classes';

@Pipe({name: 'toggleEnum'})
export class ToggleEnumPipe implements PipeTransform {
    transform(value: any, type: string): any {
        switch (type) {
            case 'ToolType': {
                return ToolType[value];
            }
            case 'SizeType': {
                return SizeType[value];
            }
            case 'InteractionType': {
                return InteractionType[value];
            }
        }
        return null;
    }
}


