import {Component, Input, ElementRef, QueryList, ViewChildren} from '@angular/core';

@Component({
    selector: 'dt-draw',
    template: require('./draw.component.html'),
    styles: [require('./draw.component.scss')]
})

export class DrawComponent {
    @Input() showCursor: boolean;

    @ViewChildren('canvas,tempCanvas,cursorCanvas') canvases: QueryList<ElementRef>;

    getCursorStyle(): string {
        return this.showCursor ? 'default' : 'none';
    }
}


