import {Injectable} from '@angular/core';
import {Subject}    from 'rxjs/Subject';
import {ToolType, SizeType, InteractionType} from '../../game/draw/engine/draw.classes';

@Injectable()
export class GameService {

    // Observable string sources
    private colorChangedSource = new Subject<string>();
    private sizeChangedSource = new Subject<SizeType>();
    private toolChangedSource = new Subject<ToolType>();
    private interactionExecutedSource = new Subject<InteractionType>();

    // Observable string streams
    colorChanged$ = this.colorChangedSource.asObservable();
    sizeChanged$ = this.sizeChangedSource.asObservable();
    toolChanged$ = this.toolChangedSource.asObservable();
    interactionExecuted$ = this.interactionExecutedSource.asObservable();

    changeColor(color: string) {
        this.colorChangedSource.next(color);
    }

    changeSize(size: SizeType) {
        this.sizeChangedSource.next(size);
    }

    changeTool(tool: ToolType) {
        this.toolChangedSource.next(tool);
    }

    executeAction(interaction: InteractionType) {
        this.interactionExecutedSource.next(interaction);
    }
}



