import {ToolType, Color, Point, Action, SizeType, InteractionType} from './draw.classes';
import {PaintUtils} from './util/paint.utils';
import {Utils} from './util/utils';
import {ColorUtils} from './util/color.utils';
import {ElementRef} from '@angular/core';

export namespace Draw {

    const defaults = {
        tool: ToolType.Pencil,
        size: SizeType.Medium,
        color: new Color(0, 0, 0, 255)
    };

    let debug = true;

    let canvas: any;
    let ctx: any;

    let currentCoordinates: Point;
    let currentAction: Action;
    let actionArray: Action[] = [];

    let isPainting: boolean = false;
    let isCursorPainting: boolean = true;
    let mainLoopToken: any;


    function startPaintLoop() {
        if (mainLoopToken === null) {
            requestAnimationFrame(paintLoop);
        }
    }

    function paintLoop() {
        mainLoopToken = requestAnimationFrame(paintLoop);
        if (isPainting) PaintUtils.paintWithTool(currentAction, ctx.temp);
        if (isCursorPainting) PaintUtils.paintCursor(currentAction, currentCoordinates, ctx.cursor);
    }

    function stopPaintLoop() {
        if (mainLoopToken !== null && !isPainting && !isCursorPainting) {
            cancelAnimationFrame(mainLoopToken);
            mainLoopToken = null;
        }
    }

    function initCurrentAction(e: any) {
        currentAction = currentAction.cleanCopy();
        currentAction.addPoint(Utils.getMouseCoordinates(e));
    }

    function paintAndSaveCurrentAction() {
        if (checkIfJustOneClick()) {
            changeActionToDefaultShape(currentAction);
        }

        PaintUtils.paintWithTool(currentAction, ctx.temp);
        saveTempContextToMainContext();
        saveActionToActionArray(currentAction);
    }

    function checkIfJustOneClick(): boolean {
        return currentAction.getPointCount() === 1 ||
            (currentAction.getPointCount() === 2 && currentAction.pointArray[0].comparePoint(currentAction.pointArray[1]));
    }

    function changeActionToDefaultShape(action: Action) {
        switch (action.tool) {
            case ToolType.Circle:
            case ToolType.Triangle:
                action.pointArray[1] = new Point(
                    action.pointArray[0].x + defaults.size * 8 / 5,
                    action.pointArray[0].y + defaults.size * 8 / 5);
                break;
            case ToolType.Rectangle:
            case ToolType.Line:
                action.pointArray[1] = new Point(
                    action.pointArray[0].x + defaults.size * 4,
                    action.pointArray[0].y + defaults.size * 4
                );
        }
    }

    function saveActionToActionArray(action: Action) {
        if (debug) console.log('Save:', action);
        actionArray.push(action);
    }

    function saveTempContextToMainContext() {
        ctx.main.drawImage(canvas.temp, 0, 0, ctx.main.canvas.width, ctx.main.canvas.height);
        PaintUtils.clearContext(ctx.temp);
    }

    /**
     * Handles the received events by the temp canvas.
     * @param e
     */
    function onEvent(e: any) {
        e.preventDefault();
        switch (e.type) {
            case 'mouseenter':
                onMouseEnter(e);
                break;
            case 'mousedown':
            case 'touchstart':
                onMouseDown(e);
                break;
            case 'mouseup':
            case 'touchend':
                onMouseUp(e);
                break;
            case 'mouseleave':
            case 'touchcancel':
                onMouseLeave(e);
                break;
            case 'mousemove':
            case 'touchmove':
                onMouseMove(e);
                break;
            case 'click':
                onMouseClick(e);
                break;
        }
    }

    /**
     * Decides what to do, when the cursor enters the canvas.
     * @param e
     */
    function onMouseEnter(e: any) {
        if (currentAction.tool === ToolType.Pencil || currentAction.tool === ToolType.Eraser) {
            if (debug) console.log('Start cursor painting');
            isCursorPainting = true;
            startPaintLoop();
        }
    }

    /**
     * Decides what to do with action, which have a starting point.
     * @param e
     */
    function onMouseDown(e: any) {
        if (currentAction.tool === ToolType.PaintBucket) return;
        if (debug) console.log('Start painting');

        isPainting = true;
        initCurrentAction(e);
        startPaintLoop();
    }

    /**
     * Decides what to do with action, which have a transition state.
     * @param e
     */
    function onMouseMove(e: any) {
        currentCoordinates = Utils.getMouseCoordinates(e);

        if (isPainting) {
            if (debug) console.log('Move');

            if (currentAction.tool === ToolType.Pencil || currentAction.tool === ToolType.Eraser) {
                currentAction.addPoint(currentCoordinates);
            } else {
                currentAction.pointArray[1] = currentCoordinates;
            }
        }
    }

    /**
     * Decides what to do with action, which have an endpoint.
     * @param e
     */
    function onMouseUp(e: any) {
        if (currentAction.tool === ToolType.PaintBucket) return;

        if (isPainting) {
            if (debug) console.log('Stop painting');

            isPainting = false;
            stopPaintLoop();
            paintAndSaveCurrentAction();
        }
    }

    /**
     * Decides what to do with action, which have been interrupted.
     * @param e
     */
    function onMouseLeave(e: any) {
        if (currentAction.tool === ToolType.Pencil ||
            currentAction.tool === ToolType.Eraser) {
            if (debug) console.log('Stop cursor painting');

            isCursorPainting = false;
            PaintUtils.clearContext(ctx.cursor);
            stopPaintLoop();

        }

        if (currentAction.tool === ToolType.Line ||
            currentAction.tool === ToolType.Circle ||
            currentAction.tool === ToolType.Triangle ||
            currentAction.tool === ToolType.Rectangle) {
            if (isPainting) {
                if (debug) console.log('Interrupt painting');

                isPainting = false;
                PaintUtils.clearContext(ctx.temp);
                stopPaintLoop();
            }
        } else {
            onMouseUp(e);
        }
    }

    /**
     * Decides what to do with action, which consists of one point.
     * @param e
     */
    function onMouseClick(e: any) {
        if (currentAction.tool !== ToolType.PaintBucket) return;
        if (debug) console.log('Click');

        if (currentAction.tool === ToolType.PaintBucket) {
            initCurrentAction(e);
            if (PaintUtils.paintWithTool(currentAction, ctx.main))
                saveActionToActionArray(currentAction);
        }
    }

    /**
     * Initializes the canvas and temp canvas.
     */
    export function init(elementRefs: ElementRef[], tool?: ToolType, size?: SizeType, color?: string) {
        if (debug) console.log('Init');

        canvas = {
            main: elementRefs[0].nativeElement,
            temp: elementRefs[1].nativeElement,
            cursor: elementRefs[2].nativeElement,
        };

        ctx = {
            main: canvas.main.getContext('2d'),
            temp: canvas.temp.getContext('2d'),
            cursor: canvas.cursor.getContext('2d')
        };

        canvas.cursor.addEventListener('click', onEvent, false);
        canvas.cursor.addEventListener('mousemove', onEvent, false);
        canvas.cursor.addEventListener('mousedown', onEvent, false);
        canvas.cursor.addEventListener('mouseup', onEvent, false);
        canvas.cursor.addEventListener('mouseenter', onEvent, false);
        canvas.cursor.addEventListener('mouseleave', onEvent, false);
        canvas.cursor.addEventListener('touchstart', onEvent, false);
        canvas.cursor.addEventListener('touchmove', onEvent, false);
        canvas.cursor.addEventListener('touchend', onEvent, false);
        canvas.cursor.addEventListener('touchcancel', onEvent, false);

        currentAction = new Action(
            tool ? tool : defaults.tool,
            size ? size : defaults.size,
            color ? ColorUtils.hexToRgba(color) : defaults.color);

        Utils.setupRequestFrame();
        PaintUtils.resetCanvas(ctx.main);
    }

    function clearContextAndActions() {
        if (debug) console.log('Clear');
        PaintUtils.resetCanvas(ctx.main);
        actionArray = [];
    }

    function redrawActions() {
        console.log(actionArray);
        PaintUtils.resetCanvas(ctx.main);
        for (let i = 0; i < actionArray.length; i++) {
            const action = actionArray[i];
            if (action.tool = ToolType.PaintBucket)
                PaintUtils.paintWithTool(action, ctx.main);
            else {
                PaintUtils.paintWithTool(action, ctx.temp);
                saveTempContextToMainContext();
            }
        }
    }

    function undoLastAction() {
        const length = actionArray.length;

        if (length > 0) {
            if (debug) console.log('Undo');
            actionArray.pop();
            redrawActions();
        }
    }

    export function changeColor(hexColor: string) {
        currentAction.color = ColorUtils.hexToRgba(hexColor);
        if (debug) console.log('New color: ' + hexColor);
    }

    export function changeSizeType(sizeType: SizeType) {
        currentAction.size = sizeType;
        if (debug) console.log('New size: ' + SizeType[sizeType]);
    }

    export function changeToolType(toolType: ToolType) {
        currentAction.tool = toolType;
        if (debug) console.log('New tool: ' + ToolType[toolType]);
    }

    export function executeInteraction(interaction: InteractionType) {
        switch (interaction) {
            case InteractionType.Clear:
                clearContextAndActions();
                break;
            case InteractionType.Undo:
                undoLastAction();
                break;
        }
    }
}




