import {Component, ViewChild, ElementRef} from '@angular/core';

@Component({
    selector: 'dt-chat',
    template: require('./chat.component.html'),
    styles: [require('./chat.component.scss')]
})

export class ChatComponent {
    @ViewChild('divMessages') divMessages: ElementRef;

    messageArray: string[] = [];

    onEnter(text: string) {
        this.messageArray.push(text);
        this.scrollDown(this.divMessages);
    }

    private scrollDown(element: ElementRef) {
        let native = element.nativeElement;
        native.scrollTop = native.scrollHeight;
    }
}


