import {Component, OnDestroy, AfterViewInit, ViewChild} from '@angular/core';
import {GameService} from '../common/service/game.service';
import {SizeType, ToolType} from './draw/engine/draw.classes';
import {Subscription} from 'rxjs';
import {Draw} from  './draw/engine/draw.core';
import {DrawComponent} from './draw/draw.component';

@Component({
    selector: 'dt-game',
    styles: [require('./game.component.scss')],
    template: require('./game.component.html'),
    providers: [GameService]
})

export class GameComponent implements AfterViewInit, OnDestroy {
    currentTool: ToolType = ToolType.Pencil;
    currentSize: SizeType = SizeType.Medium;
    currentColor: string = '#000000';
    showCursor: boolean;

    @ViewChild(DrawComponent) drawComponent: DrawComponent;

    private subscriptions: Subscription[] = [];

    constructor(private gameService: GameService) {
        this.subscriptions.push(gameService.colorChanged$.subscribe(color => Draw.changeColor(color)));
        this.subscriptions.push(gameService.sizeChanged$.subscribe(size => Draw.changeSizeType(size)));
        this.subscriptions.push(gameService.toolChanged$.subscribe(tool => {
            this.showCursor = !(tool === ToolType.Eraser || tool === ToolType.Pencil);
            Draw.changeToolType(tool);
        }));
        this.subscriptions.push(gameService.interactionExecuted$.subscribe(interaction => Draw.executeInteraction(interaction)));
    }

    ngAfterViewInit(): void {
        Draw.init(
            this.drawComponent.canvases.toArray(),
            this.currentTool,
            this.currentSize,
            this.currentColor
        );
    }

    ngOnDestroy(): void {
        this.subscriptions.forEach(sub => sub.unsubscribe());
    }
}


