import {Component} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {RoomsDataService} from '../../../common/dataservice/rooms.dataservice';
import {ToastService} from '../../../common/service/toast.service';

@Component({
    selector: 'dt-create-form',
    styles: [require('./create-form.component.scss')],
    template: require('./create-form.component.html')
})

export class CreateFormComponent {
    isSubmitting: boolean;

    roomSizes: number[] = [
        2, 3, 4, 5, 6, 7, 8
    ];
    selectedRoomSize: number = 4;

    createForm: FormGroup;

    constructor(private formBuilder: FormBuilder,
                private roomsDataService: RoomsDataService,
                private toastService: ToastService) {

        this.createForm = formBuilder.group({
            'maxPlayers': [this.selectedRoomSize, Validators.required],
            'isPrivate': [false],
            'password': [{value: '', disabled: true}, [Validators.required, Validators.maxLength(16)]]
        });
    }

    onSubmit(): void {
        if (this.isSubmitting) return;

        this.isSubmitting = true;
        this.toastService.showLoading();

        this.roomsDataService.createRoom(this.createForm.value).subscribe(
            data => {
                console.log(data);
                this.isSubmitting = false;
            },
            error => {
                this.onError(error);
            },
            () => {
                this.onComplete();
            });
    }

    private onError(error: Error) {
        this.toastService.warning(error.message);
        this.isSubmitting = false;
    }

    private onComplete() {
        this.toastService.clearToasts();
        this.isSubmitting = false;
    }


    setRoomSize(size: number) {
        this.selectedRoomSize = size;
    }

    togglePasswordInput() {
        let ctrl = this.createForm.get('password');
        ctrl.enabled ? ctrl.disable() : ctrl.enable();
        ctrl.setValue('');
    }
}


