import {Component} from '@angular/core';
import {ToastService} from '../common/service/toast.service';
import {RoomsDataService} from '../common/dataservice/rooms.dataservice';
import {Room} from '../common/util/classes';

@Component({
    selector: 'dt-lobby',
    styles: [require('./lobby.component.scss')],
    template: require('./lobby.component.html')
})

export class LobbyComponent {
    roomArray: Room[];
    mockArray: Room[] = [];
    rows: any = [];

    constructor(private roomsDataService: RoomsDataService,
                private toastService: ToastService) {
        this.generateMockArray();
        this.roomArray = this.mockArray;
        this.generateRows(this.roomArray.length);
    }

    getRooms() {
        this.roomsDataService.getRoomArray().subscribe(
            data => {
                console.log(data);
                this.roomArray = this.mockArray;
                this.generateRows(this.roomArray.length);
            },
            error => {
                this.toastService.warning(error.message);
            });
    }

    private generateRows(length: number) {
        this.rows = [];
        for (let i = 0; i < length; i += 2) {
            let row = {start: i, end: Math.min(i + 2, length)};
            this.rows.push(row);
        }
    }

    private generateMockArray() {
        for (let i = 0; i < 20; i++) {
            this.mockArray.push({
                id: i + '',
                players: i % 8,
                maxPlayers: 8
            });
        }
    }
}


