import {Color} from '../draw.classes';

export class ColorUtils {
    static rgbaToString(color: Color): string {
        return 'rgba(' + [color.r, color.g, color.b].join(',') + ',1)';
    }

    static hexToRgba(hex: any): Color {
        let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16),
            a: 255
        } : null;
    }

    static getPixelColor(index: number, data: any): Color {
        return {
            r: data[index],
            g: data[index + 1],
            b: data[index + 2],
            a: 255
        };
    }

    static compareColor(index: number, data: any, color: Color, tolerance: number): boolean {
        let pixelColor = this.getPixelColor(index, data);

        return Math.abs(pixelColor.r - color.r) <= tolerance
            && Math.abs(pixelColor.g - color.g) <= tolerance
            && Math.abs(pixelColor.b - color.b) <= tolerance;
    }

    static colorPixel(index: number, data: any, color: Color) {
        data[index] = color.r;
        data[index + 1] = color.g;
        data[index + 2] = color.b;
        data[index + 3] = 255;
    }
}



