import {Injectable, EventEmitter} from '@angular/core';
import {Router} from '@angular/router';
import {SocketEvent} from '../constants';
import {ToastService} from './toast.service';

@Injectable()
export class SocketService {
    private socket: any;
    private userName: string;
    private userList: string[];

    constructor(private router: Router,
                private toastService: ToastService) {
    }

    init(userName: string, emitter: EventEmitter<boolean>) {
        let ref = this;
        this.userName = userName;
        this.socket = io(process.env.API_URL, {
            reconnectionAttempts: 1,
            query: 'userName=' + this.userName
        });

        this.initSocketEventListeners(this.socket, ref, emitter);

        this.initBackendResponseListeners(this.socket, ref);
    }

    private initSocketEventListeners(socket: any, ref: SocketService, emitter: EventEmitter<boolean>) {
        Object.getOwnPropertyNames(SocketEvent).forEach(key => {
            socket.on(SocketEvent[key], function () {
                let socketEvent = SocketEvent[key];

                console.log('socket :: ' + socketEvent);
                if (socketEvent === SocketEvent.CONNECT) {
                    emitter.emit(true);
                } else if (socketEvent === SocketEvent.RECONNECT_FAILED) {
                    ref.toastService.error(socketEvent);
                }
            });
        });
    }

    private initBackendResponseListeners(socket: any, ref: SocketService) {

    }
}


