import {Component} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {RoomsDataService} from '../../../common/dataservice/rooms.dataservice';
import {ToastService} from '../../../common/service/toast.service';

@Component({
    selector: 'dt-join-form',
    styles: [require('./join-form.component.scss')],
    template: require('./join-form.component.html')
})

export class JoinFormComponent {
    isSubmitting: boolean;

    joinForm: FormGroup;

    constructor(private formBuilder: FormBuilder,
                private toastService: ToastService,
                private roomsDataService: RoomsDataService) {

        this.joinForm = formBuilder.group({
            'id': [null, [Validators.required, Validators.maxLength(5), Validators.pattern('^[1-9][0-9]{0,4}')]],
            'password': [null, [Validators.required, Validators.maxLength(16)]],
        });
    }

    onSubmit(): void {
        if (this.isSubmitting) return;

        this.isSubmitting = true;
        this.toastService.showLoading();

        this.roomsDataService.joinRoom(this.joinForm.value).subscribe(
            data => {
                console.log(data);
            },
            error => {
                this.onError(error);
            },
            () => {
                this.onComplete();
            });
    }

    private onError(error: Error) {
        this.toastService.warning(error.message);
        this.isSubmitting = false;
    }

    private onComplete() {
        this.toastService.clearToasts();
        this.isSubmitting = false;
    }
}


