import {Component, Output, EventEmitter} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {RootDataService} from '../../common/dataservice/root.dataservice';
import {ToastService} from '../../common/service/toast.service';
import {CookieService} from 'angular2-cookie/services/cookies.service';
import {CookieKey} from '../../common/constants';

@Component({
    selector: 'dt-guest-form',
    styles: [require('./guest-form.component.scss')],
    template: require('./guest-form.component.html')
})

export class GuestFormComponent {
    isSubmitting: boolean = false;

    @Output() submittedEmitter = new EventEmitter<boolean>();

    guestForm: FormGroup;

    constructor(private formBuilder: FormBuilder,
                private cookieService: CookieService,
                private rootDataService: RootDataService,
                private toastService: ToastService) {
        this.initializeForm();
    }

    onSubmit(): void {
        if (!this.isSubmitting) {
            this.isSubmitting = true;

            this.toastService.showLoading();

            this.rootDataService.getApiStatus().subscribe(
                data => {
                    console.log(data);
                },
                error => {
                    this.onError(error);
                },
                () => {
                    this.onComplete();
                });
        }

    }

    private onError(error: Error) {
        this.toastService.warning(error.message);
        this.isSubmitting = false;
    }

    private onComplete() {
        this.cookieService.put(CookieKey.USER_NAME, this.guestForm.value.userName);
        this.toastService.clearToasts();
        this.submittedEmitter.emit(true);
        this.isSubmitting = false;
    }

    private initializeForm() {
        this.guestForm = this.formBuilder.group({
            'userName': [null, [Validators.required, Validators.maxLength(16)]]
            // TODO validate whitespaces, trim, language filter, special character
        });
    }
}


