import {Response} from '@angular/http';
import {Observable} from 'rxjs';
import {Error} from './classes';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';

export class DataServiceUtil {
    static extractData(res: Response) {
        console.log(res);
        return res.json();
    }

    static handleError(error: Response | any): ErrorObservable {
        let err: Error;
        if (error instanceof Response) {
            let body = error.json() || null;
            if ( body.message ) {
                err = new Error(body.status, body.message);
            } else {
                err = new Error(0, 'The server is unreachable, try later');
            }
        } else {
            err = new Error(0, error.message ? error.message : error.toString());
        }
        return Observable.throw(err);
    }
}



