import {NgModule}      from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {FormsModule}   from '@angular/forms';
import {ReactiveFormsModule} from '@angular/forms';
import {HttpModule, JsonpModule} from '@angular/http';

import {AppRoutingModule} from './app-routing.module';
import {LoginComponent} from './login/login.component';
import {AppComponent} from './app.component';
import {GameComponent} from './game/game.component';
import {ColorsComponent} from './game/colors/colors.component';
import {ChatComponent} from './game/chat/chat.component';
import {DrawComponent} from './game/draw/draw.component';
import {UsersComponent} from './game/users/users.component';
import {ToolsBlockComponent} from './game/tools/tools.component';
import {ToggleEnumPipe} from './common/pipe/enum.pipe';
import {GuestFormComponent} from './login/guest/guest-form.component';
import {CreateFormComponent} from './login/options/create/create-form.component';
import {OptionsComponent} from './login/options/options.component';
import {JoinFormComponent} from './login/options/join/join-form.component';
import {SocketService} from './common/service/socket.service';
import {RoomsDataService} from './common/dataservice/rooms.dataservice';
import {RootDataService} from './common/dataservice/root.dataservice';
import {ToastyModule} from 'ng2-toasty';
import {ToastService} from './common/service/toast.service';
import {CookieService} from 'angular2-cookie/services/cookies.service';
import {LobbyComponent} from './lobby/lobby.component';
import {RoomComponent} from './common/component/room/room.component';

@NgModule({
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        JsonpModule,
        ReactiveFormsModule,
        AppRoutingModule,
        ToastyModule.forRoot()
    ],
    declarations: [
        AppComponent,
        ChatComponent,
        ColorsComponent,
        DrawComponent,
        GameComponent,
        GuestFormComponent,
        CreateFormComponent,
        JoinFormComponent,
        LobbyComponent,
        LoginComponent,
        OptionsComponent,
        RoomComponent,
        UsersComponent,
        ToolsBlockComponent,
        ToggleEnumPipe
    ],
    providers: [
        CookieService,
        ToastService,
        SocketService,
        RoomsDataService,
        RootDataService
    ],
    bootstrap: [AppComponent]
})

export class AppModule {
}



