import {Component, Input} from '@angular/core';
import {GameService} from '../../common/service/game.service';

@Component({
    selector: 'dt-colors',
    template: require('./colors.component.html'),
    styles: [require('./colors.component.scss')]
})

export class ColorsComponent {
    colors: string[] = [
        '#000000',
        '#424242',
        '#BDBDBD',
        '#F5F5F5',
        '#FFF9C4',
        '#FFEB3B',
        '#FFC107',
        '#FF9800',
        '#FF5722',
        '#F44336',
        '#B71C1C',
        '#FF4081',
        '#E91E63',
        '#9C27B0',
        '#673AB7',
        '#1A237E',
        '#0D47A1',
        '#2196F3',
        '#00E5FF',
        '#009688',
        '#1B5E20',
        '#33691E',
        '#4CAF50',
        '#76FF03',
        '#CDDC39',
        '#F1D4AF',
        '#774F38',
        '#3E2723'
    ];

    @Input() currentColor: string;

    constructor(private gameService: GameService) {
    }

    changeColor(hexColor: string) {
        this.currentColor = hexColor;
        this.gameService.changeColor(hexColor);
    }
}


